        </el-row>
        <el-row>
          <el-col :span='7'>
              <el-form-item label='黑名单类型' prop='plateColor' class='laeblStyle'>
                  <el-select v-model='addMsgForm.blackType' style='width:60%' @change='hmdMsg(addMsgForm.blackType)'>
                    <el-option
                    v-for='item in blackTypeOptions'
                    :key='item.id'
                    :label='item.label'
                    :value='item.value'>
                    </el-option>
                  </el-select>
                </el-form-item>
          </el-col>
          <el-col :span='1'>
              &nbsp;
            </el-col>
      <div v-show='qfShow'>
        <el-col :span='7'>
           <el-form-item label='欠费次数' class='laeblStyle'>
             <el-input v-model='addMsgForm.arrearageCount' class='handle-input'></el-input>
           </el-form-item>
        </el-col>
          <el-col :span='1'>
              &nbsp;
            </el-col>
        <el-col :span='7'>
           <el-form-item label='欠费总额' class='laeblStyle'>
             <el-input v-model='addMsgForm.arrearageTotal' class='handle-input'></el-input>
