</template>
<script>
  import moment from 'moment'
  import qs from 'qs'
  export default {
    methods: {
      //黑名单删除
      handleDelMsg(index,row){
        console.log(index)
        console.log(row)
        var self=this;
        var url=self.baseUrl+"blackList/cancel?id="+row.id;
        self.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          self.$axios.get(url).then(function(res){
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.getCarMsg();
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
          }).catch(err=>console.log(err))
        }).catch(() => {
          self.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });






     

      },
      editHMD(val){
        var self=this;
        if(val=='1'){
          self.editHMD1=true;
        }else{
          self.editHMD1=false;
        }
      },
      hmdMsg(val){
        var self=this;
        console.log(val)
        if(val=='1'){
          self.qfShow=true;
        }else{
          self.qfShow=false;
        }
      },
      //黑名单管理
      saveInsertMsg3(val){
        var self=this;
        var params;
        console.log("val5555555")
        console.log(val.plateColorName)
        var plateColorName;
        var a=self.pdCarColor(val.plateColorName);
        if(a){

        }
        if(val.blackType=='1'){
          params={
            "id":val.id,
            "parkingId":val.parkingId,
            "plateNo":val.plateNo,
            "plateColorName":plateColorName,
            "blackType":val.blackType,
            "arrearageCount":val.arrearageCount,
            "arrearageTotal":val.arrearageTotal,
            "remark":val.remark
          }
        }else{
            params={
            "id":val.id,
            "parkingId":val.parkingId,
            "plateNo":val.plateNo,
            "plateColorName":val.plateColorName,
            "blackType":val.blackType,
            "arrearageCount":'',
            "arrearageTotal":'',
            "remark":val.remark
            }
          }
        var url=self.baseUrl+'blackList/update';
        self.$axios.post(url,params).then(function(res){
          console.log(res.data)
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.dialogVisible3=false;
            self.getCarMsg();
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(err=>console.log(err))
      },
      // 车牌颜色
		getCarColor(){
			var self=this;
			var url=self.baseUrl+"vehicleInfo/plateColorList"
			self.$axios.get(url).then(function(res){
				self.plateColorOptionsShow=true;
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.plateColorOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].plateColor,
							value:res.data.data[i].plateColorNo,
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
		},
      addBlackList(){
        var self=this;
        self.dialogVisibleAddRule=true;
        var url=self.baseUrl+"blackList/add";
        self.$axios.post().then(function(res){
          console.log("黑名单管理")
          console.log(res.data)
        }).catch(err=>console.log(err))
      },
      handleBJ(val){
        console.log(val);
        var self=this;
        var url=self.baseUrl+"feeRule/update";
        val.feeRuleConfigs=val.scArr;
        self.$axios.post(url,val).then(function(res){
          console.log(res.data)
          self.$message({
            message:res.data.message,
            type:"info"
          })
          self.dialogVisible3=false;
        }).catch(err=>console.log(err));
      },
      cancelBJ(){
        this.dialogVisible3=false;
      },
      getCycleType(){
        var self=this;
        var url=self.baseUrl+"feeRule/initCycleType"
        self.$axios.get(url).then(function(res){
          self.cycleTypeIdShow=true;
          for(var i=0;i<res.data.data.length;i++){
          self.cycleTypeIdOption[i]={
            id:res.data.data[i].cycleTypeId,
            value:res.data.data[i].cycleTypeId,
            label:res.data.data[i].cycleTypeName,
          }
          }
          console.log(self.cycleTypeIdOption)
        }).catch(err=>console.log(err))
      },
      saveEditMsg(val){
        var self=this;
        var url=self.baseUrl+"feeRule/updateRuleForVehicleType";
        var params={
          "parkingId":self.changeMsgForm.parkingId,
          "id":self.changeMsgForm.id,
          "vehicleTypeId":self.changeMsgForm.vehicleTypeId,
          "feeRuleId":self.changeMsgForm.feeRuleId,
        }
        self.$axios.post(url,params).then(function(res){
          console.log(res.data)
          self.$message({
            message:res.data.message,
            type:"info"
          })
          self.dialogVisible3=false;
        }).catch(function(err){
          console.log(err);
        })
      },
      cancel2(){
        this.dialogVisibleAddRule=false;
      },
      cancel3(){
        this.dialogVisible3=false;
      },
      saveInsertMsg(val){
        var self=this;
        var url=self.baseUrl+"blackList/add";
        var a=self.pdCarColor(val.plateColor);
        if(a){
        }else{
          this.$message({
            message:"请选择车牌颜色!",
            type:"info"
          })
          return;
        }
        var params={
          "parkingId":self.addMsgForm.parkingId,
          "plateNo":self.addMsgForm.plateNo,
          "plateColorName":self.plateColorOptions[(self.addMsgForm.plateColor)-1].label,
          "blackType":self.addMsgForm.blackType,
          "remark":self.addMsgForm.remark,
        };

        if(val=='1'){
          params.arrearageCount=self.addMsgForm.arrearageCount;
          params.arrearageTotal=self.addMsgForm.arrearageTotal;
        }else{
          params.arrearageCount=''
          params.arrearageTotal=''
        }
        console.log(params);
        self.$axios.post(url,params).then(function(res){
          console.log(res.data)
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.dialogVisibleAddRule=false;
            self.search();
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(function(err){
          console.log(err);
        })
      },
      addMsg(){
        var self=this;
        self.dialogVisibleAddRule=true;
      },
      //下拉列表事件
      sendParkId(val){
        console.log(val)
        this.ruleType(val);
      },
      // 收费类型
      ruleType(val){
        var self=this;
        self.feeRuleIdOptionsShow=false;
        var url=self.baseUrl+"feeRule/initRuleList/"+val
        self.$axios.get(url).then(res=>{
          console.log('ruleType   res')
          console.log(res.data.data)
          self.feeRuleIdOptionsShow=true;
          for(var i=0;i<res.data.data.length;i++){
            self.feeRuleIdOptions[i]={
              id:res.data.data[i].id,
              label:res.data.data[i].feeRule,
              value:res.data.data[i].id
            }
          }
          console.log(self.feeRuleIdOptions)
        }).catch(err=>{
          console.log(err);
        })
      },
      search(){
        var self=this;
        var params={
          "parkingId":self.getRuleForm.parkingId,
          "currentPage":self.currentPage,
          "pageSize":self.pageSize,
          "plateNo":self.getRuleForm.plateNo,
            "plateColorName":self.detailMsgForm.plateColor
        };
        self.axiosMsg(params);
      },
      cancel(){
        this.dialogVisible3=false;
      },

      getParkingId(){
        var self=this;   
			var url=self.baseUrl+"vehicleInfo/initParkingList?userId="+localStorage.getItem("userId")
			self.$axios.get(url).then(function(res){
        self.parkingIdOptionsShow=true;
        if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.parkingIdOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].parkingName,
							value:res.data.data[i].id
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
      },
      saveMsg(val){
        var self=this;
        console.log('this.detailForm2.plateColorNo');
        console.log(self.detailForm2.vehicleType);
          var params={
            "id":self.row.id,
            "parkingId":self.detailForm2.parkingId,
            "vehicleTypeId":String(self.detailForm2.vehicleType),
            "plateNo":self.detailForm2.plateNo,
            "plateColorNo":self.detailForm2.plateColorNo,
            "plateColor": self.detailForm2.plateColor,
            "vehicleModel":self.detailForm2.vehicleModel,

            "vehicleOwner":self.detailForm2.vehicleOwner,
            "ownerIdNo":self.detailForm2.ownerIdNo,
            "ownerGender":self.detailForm2.ownerGender,
            "ownerBirth":self.detailForm2.ownerBirth,
            "ownerPhone":self.detailForm2.ownerPhone,
            "ownerAddress":self.detailForm2.ownerAddress,
            "rfidNo":self.detailForm2.rfidNo,
          }
        self.axiosChangeCar(params);
      },
      axiosChangeCar(params){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/updateVehicle";
        self.$axios.post(url,params).then(function(res){
          console.log('res.data.......');
          console.log(res.data);
        }).catch(function(err){
          console.log(err);
        })              
      },
      currentChange(currentPage){
          this.getTime();
          var self=this;
          this.currentPage=currentPage;
            var url='http://58.210.46.54:8000/listGcjlData';
            var paramString={
          "parkingId":self.getRuleForm.parkingId,
          "currentPage":self.currentPage,
          "pageSize":self.pageSize,
          "plateNo":self.getRuleForm.plateNo,
            "plateColorName":self.detailMsgForm.plateColor
            }
            var jsonParams=JSON.stringify(paramString);
          this.axiosMsg(jsonParams);
      },
      handleSizeChange(val){
          this.pageSize=val;
          var self=this;
          var url='http://58.210.46.54:8000/listGcjlData';
          var str={
            "parkingId":self.getRuleForm.parkingId,
          "currentPage":self.currentPage,
          "pageSize":self.pageSize,
          "plateNo":self.getRuleForm.plateNo,
            "plateColorName":self.detailMsgForm.plateColor
          }
          var jsonParams=JSON.stringify(str);
           this.axiosMsg(jsonParams);
          console.log(`每页显示${val}条`)
      },
      axiosMsg(params){
        var self=this;
        this.paginationShow=true;
        this.$axios({
          method:"post",
          url:self.baseUrl+"blackList/list",
          headers:{
            "Content-Type":"application/json"
          },
          data:params
        }).then(function(response){
          self.total=response.data.data.total;
          self.tableCar=response.data.data.records;
          console.log("response黑名单...")
          console.log(self.tableCar)
          for(var i=0;i<self.tableCar.length;i++){
            (self.tableCar[i].blackType=='1')?
            self.tableCar[i].blackType="逃费":
            self.tableCar[i].blackType='套牌'
          }
        }).catch(function(err){
          console.log(err);
        })
      },
      momentTime(val){
          return moment(val).format("YYYY-MM-DD HH:mm:ss");
      },
      momentTime1(val){
          return moment(val).format("YYYY-MM-DD");
      },
      getCarMsg(){
        var self=this;
          //查询，获取参数
          var str={
            'parkingId':self.getRuleForm.parkingId,
            "pageSize":10,
            "currentPage":1,
            "plateNo":self.getRuleForm.plateNo,
            "plateColorName":self.detailMsgForm.plateColor
          }
          var jsonParams=JSON.stringify(str);
          this.axiosMsg(jsonParams);
      },
      pdCarColor(val){
        if(!Number(val)){
          return false;
        }else{
          return true;
        }
      },
      handleDetailMsg(index,row,scope){
        var self=this;
        this.dialogVisible3=true;
        self.sfgzRow=row;
        console.log('row.....')
        console.log(row)
        //调用回显;
        var url=self.baseUrl+"blackList/detail?id="+row.id;
        self.$axios.get(url).then(function(res){
          self.changeMsgForm=res.data.data;
        })
      },
      handleChangeMsg(index,row,scope){
        var self=this;
        self.dialogVisible2=true;
        var url=self.baseUrl+"blackList/detail?id="+row.id;
        self.$axios.get(url).then(function(res){
          self.detailMsgForm=res.data.data;
          console.log(res.data.data)
          console.log(res.data.data.blackType)
          if(res.data.data.blackType=='1'){
            self.xqShow=true;
          }else{
            self.xqShow=false;
          }
        })
      }
    },
    data() {
      return {
        editHMD1:false,
        xqShow:false,
        qfShow:false,
        blackTypeOptions:[
          {id:1,label:"逃费",value:1},
          {id:2,label:"套牌",value:2},
        ],
        plateColorOptionsShow:false,
        plateColorOptions:[],
        sfgzRow:[],
        cycleTypeIdOption:[],
        scArrShow:false,
        statusOptions:[
          {id:1,value:1,label:"正常"},
          {id:0,value:0,label:"停用"}
        ],
        getRuleForm:{},
        parkingIdOptions:[],
        parkingIdOptionsShow:false,
        feeRuleIdOptionsShow:false,
        feeRuleIdOptions:[],
        vehicleTypeOptions:[],
        vehicleTypeOptionsShow:false,
        currentPage:1,
        total:0,
        tableCar:[],
        paginationShow:false,
        pageSize:10,
        dialogVisible2:false,
        detailMsgForm:{
          scArr:[]
        },
        changeMsgForm:{
          scArr:[]
        },
        dialogVisible3:false,
        addMsgForm:{},
        dialogVisibleAddRule:false,
        rowMsg:{}
      }
    },
    mounted:function(){
        this.getParkingId();
        this.getCycleType();
        this.getCarMsg();
        this.getCarColor();
    }
  }
</script>
<style scoped>
.box-card-ex{
  margin-top:20px !important;
}
   .sjInput{
    width:100px;
  }
    .plot{
        margin-bottom: 20px;
        width:100%;
    }
    ._ul{
        list-style: none;
    }
    ._li{
        cursor: pointer;
        margin-left: 20px;
        font-size: 14px;
        color:#606266;
    }

    .export_btn{
        color:#606266;
    }
    .export_button:hover .export_btn{
        color: #409eff;
    }
    .form-box-horizon {
        display: flex;
        align-items: center;
    }

    .form-box-horizon .el-form-item {
        width: 150px;
    }

    .form-box-horizon .el-select {
        width: 100%;
    }
    ._dropdown {
        margin-left: 50px;
        width: 250px;
    }

    .form-box-1 .el-form-item {
        width: 100%;
    }

    .form-box-2 .el-form-item {
        width: 25%;
    }

    .recorder-box {
        display: flex;
    }

    .recorder-box .el-form-item {
        /*width: 250px;*/
        margin-left: auto;
    }

    .form-submit-box {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
    }

    .form-submit-box .el-button {
        margin-right: 20px;
    }

    .form-item-box-grey {
        /*background-color: #8c939d;*/
    }

    .form-item-box {
        border: 1px solid #c8c7cc;
        padding: 20px 10px 10px 10px;
        margin-bottom: 20px;
    }

    .form-item-title {
        margin-bottom: 20px;
        display: flex;
        align-items: center;
    }

    .form-item-title .title-text {
        margin-left: 20px;
    }

    .form-item-title a {
        margin-left: auto;
    }

    .form-box-sub-title {
        margin-bottom: 10px;
    }

    .long-label .el-form-item {
        width: 400px;
    }

    .long-label .el-form-item label {
        width: 150px !important;
    }

    .long-label .el-form-item .el-form-item__content {
        margin-left: 150px !important;
    }

    .victim-add-btn-box {
        width: 100%;
        text-align: right;
        margin-bottom: 20px;
    }

    .victim-add-btn-box .el-button {

    }

    .dynamicBox {
        border-bottom: dashed 1px #c8c7cc;
        padding-top: 10px;
        margin-bottom: 10px;
    }
    .dynamicBox1{
        border-bottom: solid 1px #c8c7cc;
        padding-top: 10px;
        margin-bottom: 10px;
    }

    .el-button-group .el-button:not(:last-child){
        width: 140px;
    }
    .handle-input{
  width:140px;
  display: inline-block;
}
.laeblStyle{
  width:220px;
  text-align:right
}
</style>


